.App {
  margin-left: auto;
  margin-right: auto;
}

.LoginForm {
  width: 450px;
  margin-left: auto;
  margin-right: auto;
  padding: 60px;
}

.carouselSlides {
  margin-left: auto;
  margin-right: auto;
}

.carouselSlides img {
  margin-left: auto;
  margin-right: auto;
  height: 30em;
}

.homeTable {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  text-align: right;
}

.homeTable td {
  padding: 10px;
  font-size: 1.5em;
}

.purpleCard {
  --light: #8601AF;
  --dark: #2A0934;
  --other: #FEFE33;
  --otherFore: #F7F7D4;
  border: solid 1px var(--light);
}
.greenCard {
  --light: #14B884;
  --dark: #093426;
  --other: #448D76;
  --otherFore: #F7DDD4;
  border: solid 1px var(--light);
}
.orangeCard {
  --light: #FC7307;
  --dark: #341C09;
  --other: #B85B14;
  --otherFore: #D4E4F7;
  border: solid 1px var(--light);
}
.yellowCard {
  --light: #B8B814;
  --dark: #343409;
  --other: #FEFE33;
  --otherFore: #EFD4F7;
  border: solid 1px var(--dark);
}
.redCard {
  --light: #B8141F;
  --dark: #34090C;
  --other: #EA202C;
  --otherFore: #E7F7D4;
  border: solid 1px var(--dark);
}
.blueCard {
  --light: #1430B8;
  --dark: #091034;
  --other: #183BF0;
  --otherFore: #F7EBD4;
  border: solid 1px var(--dark);
}
.HomeContent .card, .HomeContent .card img, .DonateFood .card, .DonateFood .card img  {
  border-radius: 1em;
}
.HomeContent .card-body, .DonateFood .card-body {
  color: var(--otherFore);
  background: -webkit-linear-gradient(var(--light), var(--dark));
  background: -moz-linear-gradient(var(--light), var(--dark));
  background: -o-linear-gradient(var(--light), var(--dark));
  background: linear-gradient(var(--light), var(--dark));
  border-radius: 1em;
  padding-top: 40px;
  margin-top: -25px;  
}
.HomeContent .card img, .DonateFood .card img {
  z-index: 5;
}
.HomeContent .card-body:hover, .DonateFood .card-body:hover {
  background: -webkit-linear-gradient(var(--light), var(--light));
  background: -moz-linear-gradient(var(--light), var(--light));
  background: -o-linear-gradient(var(--light), var(--light));
  background: linear-gradient(var(--light), var(--light));
}
.HomeContent .card-body button, .DonateFood .card-body button {
  border: solid 1px var(--light);
  color: var(--light);
  background-color: var(--otherFore);
  font-weight: bold;
  border-radius: 1em;
  margin-left: auto;
  margin-right: auto;
}
.HomeContent .card-body button:hover, .DonateFood .card-body button:hover {
  background-color: var(--other);
  color: var(--dark);
}
.HomeContent .card-img-top:hover, .DonateFood .card-img-top:hover {
  opacity: 0.5;
}

#donation {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.donationTable , .foodPackTable{
  margin-left: auto;
  margin-right: auto;
}

.donationTable td, .foodPackTable td {
  padding: 5px;
}

.donationTable .card,  .foodPackTable .card {
  width: 18rem;
}

.donationTable img, .foodPackTable img {
  /* width: 100%; */
  height: 15em;
}

.DonateMoney .content, .DonateFood .content, .Confirmation .content, .Delivery .content, .Payment .content, .Custom .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto; 
  min-height: 50em;
}

.paymentSection form, .paymentSection table {
  margin-left: auto;
  margin-right: auto; 
  width: 80%;
}

.paymentSection table td {
  padding-right: 10px;
}

.paymentSection {
  width: 60%;
  margin-left: auto;
  margin-right: auto; 
}

.productList {
  width: 100%;
  margin-left: auto;
  margin-right: auto; 
}
.productList .card {
  width: 15em;
  margin: 20px;
  padding: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.productList .card p, .productList .card img {
  width: 100%;
}
.productList .card img {
  height: 10em;
}
.productList .card button {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.1em;
}

.MyCart .content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 50em;
}

.MyCart li {
  max-width: 40em;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
  padding-right: 50px;
}

#alertNotification {
  position: fixed;
  top: 60px;
  z-index: 10;
  min-width: 80%;
}

.Admin {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.addToOrder, .removeFromOrder {
  border: 1px solid grey; /* Green */
  padding: 0;
  padding: 2px 7px;
  margin-right: 10px;
  margin-left: 10px;
}

#myItems, #deliveryDetails {
  width: 50em;
  margin-left: auto;
  margin-right: auto;
}

.Delivery form {
  width: 50%;
  width: 50em;
  margin-left: auto;
  margin-right: auto;
}
#deliveryDetails td {
  padding: 10px;
}
.orderFilter {
  display: inline-block;
}
.orderFilter #formInput {
  width: 10em;
}

#filteredOrder, .pendingOrders .order, .completedOrders .order {
  border: 1px solid grey;
  width: 50%;
  min-width: 50em;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
}

.buttonSpace {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.buttonSpace .left {
  float: left;
}

.buttonSpace .right {
  float: right;
}

.btn {
  border-radius: 2em;
}

.form-control {
  border-radius: 2em;
}

/* Mobile css */
.Mobile .carouselSlides {
  width: 100%;
}
.Mobile h3 {
  font-size: 5vw;
}
.Mobile .carouselSlides h3 {
  font-size: 4vw;
}
.Mobile .carouselSlides p {
  font-size: 3vw;
}
.Mobile .homeTable {
  width: 100%;
}
.Mobile .homeTable td {
  width: 50%;
}
.Mobile .homeTable p {
  font-size: 4vw;  
}
.Mobile .homeTable img {
  width: 100%;
}
.Mobile .donationTable td, .Mobile .productList td {
  float: left;  
}
.Mobile .donationTable .card, .Mobile .productList .card {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.Mobile .donationTable .card img, .Mobile .productList .card img {
  width: 100%;
}
.Mobile .paymentSection {
  width: 95%;
}
.Mobile .MyCart .content, .Mobile .Confirmation .content {
  width: 95%;
}
.Mobile #myItems {
  width: 100%;
  padding: 0;
}
.Mobile .recipient {
  width: 100%;
}

